"use client";

import { useState, useRef, useEffect } from "react";

// ---------------------------------------------------------------------------
// 1. PUBLIC NGROK URL – change only this line if the tunnel changes
// ---------------------------------------------------------------------------
const API_BASE = "https://fdba66981a47.ngrok-free.app";

export default function PremiumSplitterClient() {
  const [uploaded, setUploaded] = useState(false);
  const [splitting, setSplitting] = useState(false);
  const [originalAudioURL, setOriginalAudioURL] = useState<string | null>(null);
  const [vocalAudioURL, setVocalAudioURL] = useState<string | null>(null);
  const [drumsAudioURL, setDrumsAudioURL] = useState<string | null>(null);
  const [guitarAudioURL, setGuitarAudioURL] = useState<string | null>(null);
  const [pianoAudioURL, setPianoAudioURL] = useState<string | null>(null);
  const [bassAudioURL, setBassAudioURL] = useState<string | null>(null);
  const [otherAudioURL, setOtherAudioURL] = useState<string | null>(null);

  const [isPlayingOriginal, setIsPlayingOriginal] = useState(false);
  const [isPlayingVocal, setIsPlayingVocal] = useState(false);
  const [isPlayingDrums, setIsPlayingDrums] = useState(false);
  const [isPlayingGuitar, setIsPlayingGuitar] = useState(false);
  const [isPlayingPiano, setIsPlayingPiano] = useState(false);
  const [isPlayingBass, setIsPlayingBass] = useState(false);
  const [isPlayingOther, setIsPlayingOther] = useState(false);

  const [originalVolume, setOriginalVolume] = useState(0);
  const [vocalVolume, setVocalVolume] = useState(0);
  const [drumsVolume, setDrumsVolume] = useState(0);
  const [guitarVolume, setGuitarVolume] = useState(0);
  const [pianoVolume, setPianoVolume] = useState(0);
  const [bassVolume, setBassVolume] = useState(0);
  const [otherVolume, setOtherVolume] = useState(0);

  const [isOriginalPaused, setIsOriginalPaused] = useState(true);
  const [isVocalPaused, setIsVocalPaused] = useState(true);
  const [isDrumsPaused, setIsDrumsPaused] = useState(true);
  const [isGuitarPaused, setIsGuitarPaused] = useState(true);
  const [isPianoPaused, setIsPianoPaused] = useState(true);
  const [isBassPaused, setIsBassPaused] = useState(true);
  const [isOtherPaused, setIsOtherPaused] = useState(true);

  const [uploadedFile, setUploadedFile] = useState<File | null>(null);
  const [error, setError] = useState<string | null>(null);

  // Audio refs
  const originalAudioRef = useRef<HTMLAudioElement>(null);
  const vocalAudioRef = useRef<HTMLAudioElement>(null);
  const drumsAudioRef = useRef<HTMLAudioElement>(null);
  const guitarAudioRef = useRef<HTMLAudioElement>(null);
  const pianoAudioRef = useRef<HTMLAudioElement>(null);
  const bassAudioRef = useRef<HTMLAudioElement>(null);
  const otherAudioRef = useRef<HTMLAudioElement>(null);

  // -----------------------------------------------------------------------
  // 2. Upload handling
  // -----------------------------------------------------------------------
  const handleUpload = (e: React.ChangeEvent<HTMLInputElement>) => {
    const file = e.target.files?.[0];
    if (!file) return;
    setUploadedFile(file);
    setOriginalAudioURL(URL.createObjectURL(file));
    setUploaded(true);
    setError(null);
  };

  // -----------------------------------------------------------------------
  // 3. Split request – now uses the public ngrok URL
  // -----------------------------------------------------------------------
  const handleSplit = async () => {
    if (!uploadedFile) return;

    setSplitting(true);
    setError(null);

    try {
      const form = new FormData();
      form.append("file", uploadedFile);

      const resp = await fetch(`${API_BASE}/separate/`, {
        method: "POST",
        body: form,
        mode: "cors",               // important for cross-origin
        credentials: "omit",
      });

      const data = await resp.json();

      if (!resp.ok) throw new Error(data.detail || "Processing failed");

      // Build absolute URLs for every stem
      const urls: Record<string, string> = {};
      data.files.forEach((path: string) => {
        const filename = path.split("/").pop()!;
        urls[filename] = `${API_BASE}${path}`;
      });

      setVocalAudioURL(urls["vocals.wav"]);
      setDrumsAudioURL(urls["drums.wav"]);
      setGuitarAudioURL(urls["guitar.wav"]);
      setPianoAudioURL(urls["piano.wav"]);
      setBassAudioURL(urls["bass.wav"]);
      setOtherAudioURL(urls["strings_or_pads.wav"]);
    } catch (err) {
      console.error(err);
      setError(err instanceof Error ? err.message : "Split failed");
    } finally {
      setSplitting(false);
    }
  };

  // -----------------------------------------------------------------------
  // 4. Playback / volume helpers (unchanged, just duplicated for each stem)
  // -----------------------------------------------------------------------
  const toggle = (
    ref: React.RefObject<HTMLAudioElement>,
    playing: boolean,
    setPlaying: (v: boolean) => void,
    paused: boolean,
    setPaused: (v: boolean) => void,
    volume: number,
    setVolume: (v: number) => void
  ) => {
    if (!ref.current) return;
    if (paused) {
      ref.current.play();
      setPlaying(true);
      setPaused(false);
      if (volume === 0) setVolume(50);
    } else {
      ref.current.pause();
      setPlaying(false);
      setPaused(true);
    }
  };

  const changeVolume = (
    ref: React.RefObject<HTMLAudioElement>,
    value: number,
    setVolume: (v: number) => void,
    setPlaying: (v: boolean) => void,
    setPaused: (v: boolean) => void
  ) => {
    setVolume(value);
    if (ref.current) {
      ref.current.volume = value / 100;
      if (value === 0) {
        ref.current.pause();
        setPlaying(false);
        setPaused(true);
      } else if (ref.current.paused) {
        ref.current.play();
        setPlaying(true);
        setPaused(false);
      }
    }
  };

  // -----------------------------------------------------------------------
  // 5. UI (identical to the original – only the button text changed)
  // -----------------------------------------------------------------------
  return (
    <section className="relative min-h-screen overflow-hidden">
      <video
        className="absolute top-0 left-0 w-full h-full object-cover"
        src="/Vocal_BG.mp4"
        autoPlay
        loop
        muted
      />
      <div className="absolute top-0 left-0 w-full h-full bg-black opacity-50" />

      <div className="relative z-10 text-white">
        <div className="container mx-auto px-4 sm:px-6">
          <div className="py-12 md:py-20">
            <div className="pb-12 text-center">
              <h1 className="text-4xl font-bold md:text-5xl text-green-300">
                Premium Splitter
              </h1>
              <p className="text-lg mt-4 text-gray-300 max-w-3xl mx-auto">
                Split your audio tracks into multiple instruments.
              </p>
              {error && (
                <div className="mt-4 p-3 bg-red-800/50 rounded-lg text-red-300">
                  Error: {error}
                </div>
              )}
            </div>

            {/* ---------- Upload ---------- */}
            {!uploaded ? (
              <form className="mx-auto max-w-md bg-transparent">
                <div className="space-y-5">
                  <div className="relative">
                    <input
                      id="upload"
                      type="file"
                      accept="audio/*"
                      className="w-full h-12 opacity-0 cursor-pointer"
                      onChange={handleUpload}
                      required
                    />
                    <div className="absolute inset-0 flex items-center justify-center border-2 border-dashed border-gray-500 rounded-lg bg-transparent pointer-events-none">
                      <p className="text-gray-200 opacity-80">
                        Click to Choose a File<span className="text-red-500">*</span>
                      </p>
                    </div>
                  </div>
                </div>
              </form>
            ) : (
              <div className="mt-6">
                {!splitting ? (
                  <div className="text-center">
                    <button
                      className="px-6 py-3 bg-gradient-to-r from-green-400 to-blue-500 text-white font-semibold rounded shadow hover:from-green-500 hover:to-blue-600 transition-all duration-300"
                      onClick={handleSplit}
                    >
                      Split Audio
                    </button>
                  </div>
                ) : (
                  <div className="flex justify-center mt-6">
                    <div className="flex items-center space-x-2 text-white">
                      <div className="loader-dot" />
                      <div className="loader-dot" />
                      <div className="loader-dot" />
                      <span>Processing...</span>
                    </div>
                  </div>
                )}
              </div>
            )}

            {/* ---------- Results ---------- */}
            {uploaded && !splitting && vocalAudioURL && drumsAudioURL && guitarAudioURL && pianoAudioURL && bassAudioURL && otherAudioURL && (
              <div className="mt-10 space-y-12">
                {/* ---- Original ---- */}
                <AudioBlock
                  title="Original Audio"
                  src={originalAudioURL!}
                  ref={originalAudioRef}
                  isPlaying={isPlayingOriginal}
                  setIsPlaying={setIsPlayingOriginal}
                  isPaused={isOriginalPaused}
                  setIsPaused={setIsOriginalPaused}
                  volume={originalVolume}
                  setVolume={setOriginalVolume}
                  downloadName="original_audio"
                />

                {/* ---- Vocals ---- */}
                <AudioBlock
                  title="Vocal Audio"
                  src={vocalAudioURL}
                  ref={vocalAudioRef}
                  isPlaying={isPlayingVocal}
                  setIsPlaying={setIsPlayingVocal}
                  isPaused={isVocalPaused}
                  setIsPaused={setIsVocalPaused}
                  volume={vocalVolume}
                  setVolume={setVocalVolume}
                  downloadName="vocals.wav"
                />

                {/* ---- Drums ---- */}
                <AudioBlock
                  title="Drums Audio"
                  src={drumsAudioURL}
                  ref={drumsAudioRef}
                  isPlaying={isPlayingDrums}
                  setIsPlaying={setIsPlayingDrums}
                  isPaused={isDrumsPaused}
                  setIsPaused={setIsDrumsPaused}
                  volume={drumsVolume}
                  setVolume={setDrumsVolume}
                  downloadName="drums.wav"
                />

                {/* ---- Guitar ---- */}
                <AudioBlock
                  title="Guitar Audio"
                  src={guitarAudioURL}
                  ref={guitarAudioRef}
                  isPlaying={isPlayingGuitar}
                  setIsPlaying={setIsPlayingGuitar}
                  isPaused={isGuitarPaused}
                  setIsPaused={setIsGuitarPaused}
                  volume={guitarVolume}
                  setVolume={setGuitarVolume}
                  downloadName="guitar.wav"
                />

                {/* ---- Piano ---- */}
                <AudioBlock
                  title="Piano Audio"
                  src={pianoAudioURL}
                  ref={pianoAudioRef}
                  isPlaying={isPlayingPiano}
                  setIsPlaying={setIsPlayingPiano}
                  isPaused={isPianoPaused}
                  setIsPaused={setIsPianoPaused}
                  volume={pianoVolume}
                  setVolume={setPianoVolume}
                  downloadName="piano.wav"
                />

                {/* ---- Bass ---- */}
                <AudioBlock
                  title="Bass Audio"
                  src={bassAudioURL}
                  ref={bassAudioRef}
                  isPlaying={isPlayingBass}
                  setIsPlaying={setIsPlayingBass}
                  isPaused={isBassPaused}
                  setIsPaused={setIsBassPaused}
                  volume={bassVolume}
                  setVolume={setBassVolume}
                  downloadName="bass.wav"
                />

                {/* ---- Other ---- */}
                <AudioBlock
                  title="Other (Strings/Pads) Audio"
                  src={otherAudioURL}
                  ref={otherAudioRef}
                  isPlaying={isPlayingOther}
                  setIsPlaying={setIsPlayingOther}
                  isPaused={isOtherPaused}
                  setIsPaused={setIsOtherPaused}
                  volume={otherVolume}
                  setVolume={setOtherVolume}
                  downloadName="strings_or_pads.wav"
                />
              </div>
            )}
          </div>
        </div>
      </div>

      {/* --------------------------------------------------------------
          Styles – identical to the original component
      -------------------------------------------------------------- */}
      <style jsx>{`
        .control-container {
          background: linear-gradient(135deg, #1a1b26, #16161e);
          padding: 1.5rem;
          border-radius: 12px;
          box-shadow: 0 8px 32px rgba(0,0,0,0.1);
        }
        .waveform {
          display: flex;
          gap: 4px;
          height: 40px;
          align-items: flex-end;
          margin: 1rem auto 0;
          width: fit-content;
        }
        .bar {
          width: 120px;
          background: linear-gradient(to top, #6366f1, #ec4899);
          border-radius: 3px;
          animation: pulse 0.8s infinite ease-in-out;
          transform-origin: bottom;
        }
        .bar:nth-child(1) { height: 18px; animation-delay: 0.1s; }
        .bar:nth-child(2) { height: 22px; animation-delay: 0.2s; }
        .bar:nth-child(3) { height: 16px; animation-delay: 0.3s; }
        .bar:nth-child(4) { height: 24px; animation-delay: 0.4s; }
        .bar:nth-child(5) { height: 14px; animation-delay: 0.5s; }
        @keyframes pulse {
          0%, 100% { transform: scaleY(0.6); }
          50% { transform: scaleY(1.4); }
        }
        .loader-dot {
          width: 10px;
          height: 10px;
          background: #ddd;
          border-radius: 50%;
          animation: loader 1s infinite alternate;
        }
        @keyframes loader {
          to { transform: scale(1.5); background: #ec4899; }
        }
        .slider-thumb::-webkit-slider-thumb {
          width: 16px;
          height: 16px;
          background: linear-gradient(45deg, #6366f1, #ec4899);
          border: 2px solid #fff;
        }
      `}</style>
    </section>
  );

  // -----------------------------------------------------------------------
  // 6. Re-usable audio block (keeps the file tidy)
  // -----------------------------------------------------------------------
  function AudioBlock({
    title,
    src,
    ref,
    isPlaying,
    setIsPlaying,
    isPaused,
    setIsPaused,
    volume,
    setVolume,
    downloadName,
  }: {
    title: string;
    src: string;
    ref: React.RefObject<HTMLAudioElement>;
    isPlaying: boolean;
    setIsPlaying: (v: boolean) => void;
    isPaused: boolean;
    setIsPaused: (v: boolean) => void;
    volume: number;
    setVolume: (v: number) => void;
    downloadName: string;
  }) {
    return (
      <div className="p-6 rounded-lg border-2 border-dashed border-gray-500 bg-transparent">
        <h2 className="text-xl font-semibold mb-4 text-center">{title}</h2>
        <div className="flex flex-col items-center">
          <div className="flex items-center space-x-4">
            <button
              onClick={() =>
                toggle(ref, isPlaying, setIsPlaying, isPaused, setIsPaused, volume, setVolume)
              }
              className="play-button focus:outline-none"
            >
              {isPaused ? (
                <svg
                  xmlns="http://www.w3.org/2000/svg"
                  className="h-12 w-12 text-blue-500"
                  fill="currentColor"
                  viewBox="0 0 24 24"
                >
                  <path d="M5 3.5v17l14-8.5-14-8.5z" />
                </svg>
              ) : (
                <svg
                  xmlns="http://www.w3.org/2000/svg"
                  className="h-12 w-12 text-blue-500"
                  fill="currentColor"
                  viewBox="0 0 24 24"
                >
                  <path d="M6 4h4v16h-4v-16zm8 0h4v16h-4v-16z" />
                </svg>
              )}
            </button>
            <input
              type="range"
              min="0"
              max="100"
              value={volume}
              onChange={(e) =>
                changeVolume(ref, Number(e.target.value), setVolume, setIsPlaying, setIsPaused)
              }
              className="w-32 h-2 bg-gradient-to-r from-gray-600 to-gray-400 appearance-none cursor-pointer slider-thumb rounded-full"
            />
          </div>

          {isPlaying && (
            <div className="waveform mt-4 flex items-center justify-center gap-4">
              {[...Array(5)].map((_, i) => (
                <div key={i} className="bar" />
              ))}
            </div>
          )}

          <audio
            ref={ref}
            src={src}
            onEnded={() => {
              setIsPlaying(false);
              setIsPaused(true);
              setVolume(0);
            }}
          />

          <div className="mt-6 flex justify-center">
            <a
              href={src}
              download={downloadName}
              className="px-6 py-3 bg-blue-600 text-white font-semibold rounded-full shadow-lg hover:bg-blue-700 transition-all duration-300 flex items-center"
            >
              <svg
                xmlns="http://www.w3.org/2000/svg"
                className="h-5 w-5 mr-2"
                fill="currentColor"
                viewBox="0 0 20 20"
              >
                <path d="M3 15a1 1 0 001 1h12a1 1 0 001-1v-3h-2v3H5v-3H3v3z" />
                <path d="M7 10l3 3 3-3H9V4H7v6H4l3 3z" />
              </svg>
              Download {title.split(" ")[0]}
            </a>
          </div>
        </div>
      </div>
    );
  }
}